.main-login {
    width: 350px;
    height: 500px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    overflow: hidden;
    /* border: 2px solid red; */
    /* margin-top: 250px; */
}

#chk {
    display: none;
    border: 2px solid red;
}

.signup {
    position: relative;
    width: 100%;
    height: 100%;
    /* border: 2px solid red; */
}

.login-label {
    color: black;
    font-size: 2.3em;
    justify-content: center;
    display: flex;
    margin: 60px;
    font-weight: bold;
    cursor: pointer;
    transition: .5s ease-in-out;
    /* border: 2px solid red; */
}

.login-input {
    width: 60%;
    height: 20px;
    background: #e0dede;
    justify-content: center;
    display: flex;
    margin: 20px auto;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 5px;
    /* border: 2px solid red; */
}

.login-button {
    width: 60%;
    height: 40px;
    margin: 10px auto;
    justify-content: center;
    display: block;
    color: #fff;
    background: #3c4808;
    font-size: 1em;
    font-weight: bold;
    margin-top: 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    transition: .2s ease-in;
    cursor: pointer;
    /* border: 2px solid red; */
}

.login-button:hover {
    background: #7e9711;
}

.login {
    height: 460px;
    background: #eee;
    border-radius: 60% / 10%;
    transform: translateY(-180px);
    transition: .8s ease-in-out;
    /* border: 2px solid red; */
}

.login .login-label {
    color: #3c4808;
    transform: scale(.6);
}

#chk:checked~.login {
    transform: translateY(-500px);
}

#chk:checked~.login label {
    transform: scale(1);
}

#chk:checked~.signup label {
    transform: scale(.6);
}

@media screen and (max-width: 768px) {
    .main-login {
        width: 320px;
    }
}