.container-loader {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 2px solid red; */
    max-width: 960px;
    margin: 0 auto;
    margin-top: 300px;
    padding: 3rem 1rem;
}

.ldio-uowht7upd8l div {
    position: absolute;
    width: 20px;
    height: 80px;
    top: 60px;
    animation: ldio-uowht7upd8l 1s cubic-bezier(0.5, 0, 0.5, 1) infinite;
}

.ldio-uowht7upd8l div:nth-child(1) {
    transform: translate(30px, 0);
    background: #789115;
    animation-delay: -0.6s;
}

.ldio-uowht7upd8l div:nth-child(2) {
    transform: translate(70px, 0);
    background: #789115;
    animation-delay: -0.4s;
}

.ldio-uowht7upd8l div:nth-child(3) {
    transform: translate(110px, 0);
    background: #789115;
    animation-delay: -0.2s;
}

.ldio-uowht7upd8l div:nth-child(4) {
    transform: translate(150px, 0);
    background: #789115;
    animation-delay: -1s;
}

.loadingio-spinner-bars-1eztlvewhl1h {
    width: 200px;
    height: 200px;
    display: inline-block;
    overflow: hidden;
    background: #f1f2f3;
}

.ldio-uowht7upd8l {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
    /* see note above */
}

.ldio-uowht7upd8l div {
    box-sizing: content-box;
}